.mega-menu {
    position: relative;
}

.globalNav {
    position: absolute;
    width: 100%;
    padding: 25px 0;
    @include clearfix;
    z-index: 1;
    @include transition(background-color .5s ease-in-out);
    box-shadow: 0 1px 1px transparent;

    @media (min-width: $screen-sm) {
        position: fixed;

        .modal-open & {
            padding-right: $gutter;
        }
    }

    &.navbar-white {
        background-color: #fff;
        box-shadow: 0 1px 1px rgba(#000,.1);
    }
}

.globalLogo {
    float: left;
    width: 220px;

    a {
        background: url("../images/digibank.png") no-repeat;
        background-size: 100% auto;
        height: 34px;
        display: block;
        @include hide-text;
    }

    @media (max-width: $screen-sm-max) {
        width: 160px;
        position: absolute;
        top: 0;
        left: 15px;
    }
}

.globalNav {
    nav {
        float: right;
        text-align: right;
        font-weight: 500;
        margin-top: 4px;

        ul {
            list-style: none;
        }

        a {
            color: $b1;
        }

        @media (min-width: $screen-md) {
            margin-top: 7px;
        }
    }
} 


.nav {

    &.collapse {
        display: block;
    }
    
    > li {
        float: left;
        vertical-align: top;

        @media (min-width: $screen-sm) {
            &:not(:first-child) {
                margin-left: 8px;
            }
        }

        &:last-child {
            position: relative;
            font-weight: 400;
            margin-left: 18px;

            @media (min-width: $screen-md) {
                margin-left: 22px;
            }
        }

        &:not(:last-child){
            a {
                @include globalNav;
            }
        }   
    }

    @media(max-width: $screen-sm-max) {
        @include font-size($size-sm);
    }

    @media(min-width: $screen-sm) {
        display: block;
    }

    .navLanguage {
        span {
            @include spriteSvg;
            background-position: -80px 0;
            width: 9px;
            height: 6px;
            display: inline-block;
            vertical-align: 2px;
            margin-left: 6px;
        }

        @media (min-width: $screen-sm) {
           &:hover {
               .language {
                   opacity: 1;
                   visibility: visible;
               }
           } 
        }
    }

    .language {
        background: #fff;
        padding: $gutter $gutter + 15px;
        border-radius: $radius;
        position: absolute;
        right: -15px;
        top: -20px;
        width: 190px;
        box-shadow: 0 2px 5px rgba(0,0,0,.2);
        @include transition(all .25s ease-in-out);

        li {
            padding: 5px 0;
        }

        a {
            display: block;

            &:hover {
                color: $red;
            }
        }

        @media (min-width: $screen-sm) {
            opacity: 0;
            visibility: hidden;
        }
    }

    @media(max-width: $screen-xs-max) {
        // display: none !important;
        opacity: 0;
        visibility: hidden;
        position: fixed;
        z-index: 100;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        padding-top: 65px;
        background-color: rgba($b1,.9);
        // @include transition(opacity .5s);

        a {
            pointer-events: none;
        }

        > li {
            float: none;

            a {
                color: #fff;
                padding: 12px $gutter !important;
                display: inline-block;

                &:hover {
                    background-color: transparent !important;
                    padding: 12px $gutter;
                    color: $red !important;
                }
            }
        }

        .navFaq {
            padding-bottom: 10px;
        }

        .navLanguage {
            display: inline-block;

            &:before {
                position: absolute;
                content: '';
                left: 0;
                right: $gutter;
                height: 1px;
                background-color: rgba($g3,.1);
            }

            span {
                display: none;
            }

            > a {
                display: none;
            }
        }

        .language {
            position: static;
            // visibility: visible;
            // opacity: 1;
            width: auto;
            background: none;
            box-shadow: none;
            padding: 0;

            li {
                padding: 0;

                &:first-child {
                    padding-top: 10px;
                }
            }
        }
    }

    // &.in {

    //     > li {
    //         opacity: 1;
    //     }
    //     opacity: 1;
    //     visibility: visible;
    // }
}

.nav.in {
    @include animation(fadeIn .5s forwards);
    visibility: visible;

    a {
        pointer-events: auto;
    }
}
.nav.out {
    @include animation(fadeOut .2s forwards);
    visibility: visible;
    pointer-events: none;

    a {
        pointer-events: none;
    }
}

.navbar-toggle {
    @media(min-width: $screen-sm) {
        display: none;
    }

    // @include font-size($size-sm);
    // margin-top: 4px;
    float: right;
    // color: $b2;
    // font-weight: 500;
    position: relative;
    // z-index: 101;
    // text-transform: uppercase;

    // &:hover {
    //     color: $b3;
    // }

    width: 36px;
    height: 36px;
    @include hide-text;
    background-color: $red;
    margin-top: -8px;
    border-radius: 50%;
    z-index: 101;

    &:hover {
        background-color: darken($red,10);
    }

    .mdl-body & {
        color: rgba(#fff,.6);
        position: fixed;
        right: $gutter;
    }
}

// hamburger
.hamburger {
    // width: 36px;
    // height: 24px;
    // position: relative;
    // @include hide-text;
    // background-color: #000;
    margin-top: -4px;
    pointer-events: none;
    position: absolute;
    width: 14px;
    height: 2px;
    background-color: #fff;
    border-radius: 0 1px 1px 0;
    left: 9px;
    top: 21px;
    @include transition(all .3s);

    &:before, &:after {
        content: '';
        position: absolute;
        border-radius: 0 1px 1px 0;
        height: 2px;
        background-color: #fff;
        left: 0;
        @include transition(all .3s);
    }

    &:before {
        top: -6px;
        width: 130%;
        // @include transform-origin(left top);
    }

    &:after {
        bottom: -6px;
        width: 115%;
    }

    .mdl-body & {
        background-color: transparent;

        &:before {
            @include transform(translateY(6px) rotate(45deg));
            background-color: #fff;
            width: 120%;
        }
        &:after {
            @include transform(translateY(-6px) rotate(-45deg));
            background-color: #fff;
            width: 120%;
        }
    }
}

