// Setting active nav
.features li.navFeatures a,
.registration li.navRegistration a,
.deals li.navDeals a,
.about li.navAbout a,
.faq li.navFaq a {
    @media(min-width:$screen-sm) {
        background-color: $red;
        color: #fff;
    }
}

.features .in li.navFeatures a,
.registration .in li.navRegistration a,
.deals .in li.navDeals a,
.about .in li.navAbout a,
.faq .in li.navFaq a
    {color: $red;}


//- header

.header {
    @include linear-gradient(0deg, $yellow 0%, #fb0 100%);
    padding-top: $gutter*8;
    padding-bottom: $gutter*2;
    color: $b1;

    h1 {
        color: $b1;

        span {
            color: #fff;
        }
    }

    h2 {
        color: $b1;
    }

    @media (max-width: $screen-xs-max) {
        padding-top: $gutter*6;
        padding-bottom: $gutter;
    }
}