// coming soon
.deals__comingSoon {
    min-height: 200px;
    text-align: center;
    padding: $gutter*2 0 $gutter*6;

    > div {
        width: 407px;
        height: 218px;
        background: url(../images/deals-comingsoon.png) no-repeat;
        background-size: cover;
        margin: 0 auto $gutter*2;

        @media (max-width: $screen-xs-max) {
            width: 280px;
            height: 150px;
        }
    }

    .webview & {
        @include display(flex);
        @include flex-wrap(wrap);
        @include align-content(center);
        @include justify-content(center);
        min-height: 480px;
        height: 100vh;

        // > * {
        //     width: 100%;
        // }
    }
}
.deals__updated {
    @include font-size($size-xsm);
    padding-bottom: $gutter;
}

.deals__content {
    padding-top: $gutter;
    padding-bottom: $gutter*4;
}


// deal box styling
.deal-box {
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(#000,.1);
    border-radius: $radius;
    margin-bottom: $gutter*2;
    color: $b3;

    @media (max-width: $screen-sm-max) {
        @include font-size($size-sm);
    }
    @media (max-width: $screen-xs-max) {
        margin-bottom: $gutter;
    }
}



.deal-box-content {
    padding: 20px 20px 0;

}


.img-content {
    img {
        width: 100%;
        border-radius: $radius $radius 0 0;
        display: block;
    }
}

.digisavings-box {
    @include font-size($size-sm);
    text-transform: uppercase;
    color: $red;
    line-height: 24px;

    @media (max-width: $screen-sm-max) {
        @include font-size($size-xsm);
    }
}

.digi-star {
    @include spriteSvg();
    width: 24px;
    height: 24px;
    background-position: 0 -100px;
    display: inline-block;
    vertical-align: -7px;
    margin-right: 5px;
}

.deal-title {
    font-weight: 700;
    margin-bottom: 5px;
}

.read-more-box {
    margin-bottom: $gutter;
}
.action-more {
    font-weight: 500;
    @include font-size($size-sm);
}


// read more content
.deals-offer-box {
    padding-top: 20px;
    position: relative;
    margin: 0 -20px;
    border-top: 1px solid $g3;
    padding: 0 20px;
}

.deals-offer-row {
    padding: 10px 0;

    label {
        @include font-size($size-sm);
        font-weight: 500;
        color: $b2;
    }

    p {
        margin-bottom: 5px;
    }

    &:first-child {
        padding-top: 20px;
    }

    &:last-child {
        padding-bottom: 20px;
    }

    > *:last-child {
        margin-bottom: 0;
    }
}


.action-group {
    margin-bottom: 20px;
}

.social-mediabox {
    text-align: center;
    padding-bottom: $gutter;

    > a {
        display: inline-block;
        margin: 0 5px;
        width: 38px;
        height: 38px;
        @include spriteSvg();
        border: 1px solid $g2;
        border-radius: 50%;
        background-color: rgba(#fff,0);
        @include transition(background-color .25s, border-color .25s)
    }

    .btnFBShare {
        background-position: 0 -124px;

        &:hover {
            background-position: 0 -160px;
            border-color: $facebook;
            background-color: $facebook;
        }
    }

    .btnTShare {
        background-position: -36px -124px;

        &:hover {
            background-position: -36px -160px;
            border-color: $twitter;
            background-color: $twitter;
        }
    }

    .btnWTShare {
        background-position: -108px -124px;

        &:hover {
            background-position: -108px -160px;
            border-color: $whatsapp;
            background-color: $whatsapp;
        }
    }
}


// deals mobile view

.deals--mobile {
    .deal-box {
        float: left;
        @include calc(width, '50% - 8px');

        &:nth-child(odd) {
            margin-right: 8px;
            clear: left;  
        }
        &:nth-child(even) {
            margin-left: 8px;
        }  
    }
}

