// header
.headerImage {
    text-align: center;
    margin-bottom: -145px;
    @include animation(fadeInUp 1s ease-out both);

    @media (min-width: $screen-md) {
        @include animation(fadeInRight 1s ease-out both);

        img {
            width: 500px;
        }
    }

    @media (min-width: $screen-lg) {
        margin-top: -40px;
    }
    
    @media (max-width: $screen-sm-max) {
        img {
            width: 518px;
        }
    }

    @media (max-width: $screen-xs-max) {
        margin-top: $gutter;
    }
}

.headerIntro {

    @media(min-width: $screen-md) {
        margin-top: $gutter*4;
    }

    @media(min-width: $screen-lg) {
        margin-top: $gutter*6;
    }

    @media (min-width: $screen-sm) and (max-width: $screen-sm-max) {
        text-align: center;
    }

    h1 {
        margin-bottom: 10px;
        margin-left: -2px;

        @media (max-width: $screen-xs-max) {
            @include font-size($size-lg);
            line-height: 1.2;
        }
    }

    h2 {
        @include font-size($size-md);
        // margin-bottom: $gutter*2;
        line-height: 1.5;

        @media (max-width: $screen-sm-max) {
            font-weight: 400;
        }
    }

    @media (max-width: $screen-xs-max) {
        .coming-soon--apple, .coming-soon--android {
            @include transform(scale(0.8));
            margin-left: 0;
        }

        .coming-soon--android {
            margin-left: -5px;
        }
     }
}

