// digital Experience

.digitalExperience {
    &__video {
        @include linear-gradient(to bottom, #ff5b21 0, $red 100%);
    }

    &__content {
        padding: $gutter*4 0;
        @include font-size($size-md);

        @media (max-width: $screen-xs-max) {
            padding: $gutter*3 0;
            @include font-size(1.6);
        }
    }
}

.deHand {
    background: url(../images/hand-holding-phone.png) no-repeat center bottom;
    background-size: 616px auto;

    @media (max-width: $screen-sm-max) {
        background-size: 510px auto;
    }
}

.dePhone {
    background: url(../images/iphone-white.png) no-repeat;
    background-size: 314px auto;
    width: 314px;
    height: 640px;
    margin: 0 auto;
    @include transform(translateY(-$gutter*3));
    padding: 82px 22px 0;

    @media (max-width: $screen-sm-max) {
        width: 260px;
        height: 530px;
        background-size: 260px auto;
        padding: 68px 18px 0;
    }
}


// features overview
.featuresOverview {
    padding: $gutter*10 0;
    text-align: center;

    @media (max-width: $screen-sm-max) {
        padding-top: $gutter*8;
    }

    @media (max-width: $screen-xs-max) {
        padding: $gutter*6 0 $gutter*8;
        text-align: left;

        .sectionCTA {
            margin-top: $gutter;
            padding-left: 102px - $gutter;
        }
    }
}

.featuresList {
    list-style: none;

    h4 {
        color: $red;
        margin-bottom: $gutter - 10px;
    }

    @media (max-width: $screen-sm-max) {
        li {
            padding-top: $gutter;
            padding-bottom: $gutter;
            position: relative;
        }
    }

    @media (max-width: $screen-xs-max) {
        li {
            padding-left: 102px;
            padding-bottom: 0;
        }
    }
}

[class*="feature__"] {
    width: 80px;
    height: 80px;
    margin: 0 auto $gutter;
    @include spritePng;

    @media (min-width: $screen-md) {
        margin-bottom: $gutter*2;
    }

    @media (max-width: $screen-xs-max) {
        position: absolute;
        left: $gutter + 5px;
        top: $gutter;
        @include transform(scale(.9) translate( -5px, -10px));
        @include transform-origin(left top);
    }
}

.feature__ {
    &payment {
        background-position: 0 -220px;

        @media (max-width: $screen-xs-max) {
            @include transform(scale(.9) translate( -5px, -15px));
        }
    }
    &debit {background-position: -80px -220px}
    &va {
        background-position: -160px -220px; 
        // @include transform(translateY(-7px));

        // @media (max-width: $screen-xs-max) {
        //     @include transform(translateY(-7px) scale(.7));
        // }
    }
    &pfm {background-position: -240px -220px}
}

// safestBank
.safestBank {
    // hide because of removing the video section
    // @include linear-gradient(to bottom, #fff, $g4);
    padding: $gutter*4 0;

    .sectionCTA {
        padding-left: 72px + $gutter;
    }
}

.safetyImage {
    text-align: center;

    @media (max-width: $screen-sm-max) {
        margin-bottom: $gutter*3;
    }
}

.safetyPhone {
    background: url(../images/softtoken-phone-home.png);
    width: 314px;
    height: 639px;
    background-size: 100% auto;
    margin: 0 auto;
    position: relative;

    > * {
        position: absolute;
        left: 11px;
        top: 58px;
        width: 290px;
    }

    .spinner {
        position: absolute;
        width: 50px;
        height: 50px;
        left: 50%;
        margin-left: -25px;
        bottom: 121px;
        background: url(../images/spinner.png) no-repeat;
        background-size: cover;
        @include animation(rotate .4s linear infinite forwards);
    }

    @media (max-width: $screen-sm-max) {
        width: 260px;
        height: 529px;

        > * {
            width: 240px;
            top: 47px;
            left: 10px;
        }

        .spinner {
            width: 40px;
            height: 40px;
            margin-left: -20px;
            bottom: 102px;
        }
    }
}

.safetyST--registering {
    @include animation(stRegistering 6s linear infinite forwards);
}

.safetyST--done {
    @include animation(stDone 6s linear infinite forwards);
}

.safetyContent {
    @media (min-width: $screen-md) {
        margin-top: $gutter;
    }
}

.safetyFeatures {
    list-style: none;
    margin-top: $gutter*2;

    li {
        padding-left: 72px + $gutter;
        position: relative;

        &:not(:last-child) {
            margin-bottom: $gutter*2;
        }

        &.safetyDBS {
            span {background-position: 0 -224px;}
        }

        &.safetySoftToken {
            span {background-position: -72px -224px}
        }
    }

    h4 {
        color: $red;
        margin-bottom: $gutter - 10px;
    }

    span {
        width: 72px;
        height: 72px;
        @include spriteSvg();
        position: absolute;
        left: 0;
        top: 0;

        @media (max-width: $screen-xs-max) {
            @include transform(scale(0.8));
            top: -6px;
        }
    }

}

// digibank Video
.digibankVideo {
    padding-top: $gutter*4;

    &__laptop {
        background: url(../images/laptop.png) center no-repeat;
        height: 740px;
        background-size: auto 740px;
        margin-bottom: -100px;
        position: relative;
    }

    &__content {
        width: 848px;
        height: 530px;
        margin: 0 auto;
        @include transform(translateY(47px));
    }

    @media (max-width: $screen-sm-max) {
        &__laptop {
            background-size: auto 537px;
            height: 537px;
        }

        &__content {
            width: 616px;    
            height: 385px;
            @include transform(translateY(34px));
        }
    }

    @media (max-width: $screen-xs-max) {
        &__laptop {
            background-size: auto 251px;
            height: 251px;
        }

        &__content {
            width: 288px;    
            height: 180px;
            @include transform(translateY(16px));
        }
    }

    .placeholder-image {
        opacity: .75;
    }

    .about-iframevid--home {
        min-height: 186px;

        @media (min-width: $screen-sm) {
            height: 385px;
        }
    }
}


// media section
.mediaClippings {
    padding: $gutter*4 0;
    text-align: center;
    @include linear-gradient(to bottom, $g4, $g5);

    // @media (min-width: $screen-sm) {
    //     @include linear-gradient(to bottom, $g4, $g5);
    // }
}

// increase the padding when with video on top
.digibankVideo + .mediaClippings {
    padding-top: $gutter*9;
    padding-bottom: $gutter*6;
}

.mediaList {
    overflow: hidden;
    list-style: none;
    margin-bottom: 0;

    @media (max-width: $screen-xs-max) {
        li {
            margin-bottom: $gutter*3;

            &:last-child {
                margin-bottom: $gutter*2;
            }
        }
    }

    .logo {
        @media (min-width: $screen-sm) {
            height: 60px;
        }
    }

    img {
        padding-bottom: $gutter;
        max-width: 140px;
        max-height: 60px;
    }

    blockquote {
        font-style: italic;
        margin-bottom: 0;

        footer {
            font-weight: 500;
            @include font-size($size-sm);
        }
    }
}

