@import "../home";
@import "../about";
@import "../features";
@import "../registration";
@import "../deals";
@import "../media";
@import "../faq";
@import "../tnc";



.title {
    @include title(true);

    @media(max-width: $screen-xs-max) {
        @include font-size(2.4);
        line-height: 1.4;
        padding: $gutter $gutter*2 $gutter 0;

        .inside-content-box & {
            @include font-size(1.6);
        }
    }
}

// Cyber Security

.sectionSecurity {
    li:not(:last-child) {
        margin-bottom: $gutter;
    }
}

.rates {
    .inside-content-box {
        &:not(:last-child) {
            padding-bottom: 40px;
        }
    }
}


// Rates and Fees
table[class*=table] {
    margin-bottom: $gutter*3;

    @media (max-width: $screen-xs-max) {
        .w35 {
            width: 45%;
        }
    }
}
.tableFees, .tableLimit {
    td:last-child {
        width: 25%;
    }

    @media (max-width: $screen-xs-max) {

        .w35 {
            width: 45%;
        }
        td {
            // display: block;
            border-right: none;

            // &:first-child {
            //     // border-bottom: none;
            //     // padding-bottom: 0;
            // }

            &:last-child {
                padding-top: 0;
                width: auto;
            }
        }

        [data-content] {
            &:after {
                content: attr(data-content);
                display: block;
                font-weight: 400;
                color: $b5;
            }
        }
    }
}

.tableLimit {
    @media (max-width: $screen-xs-max) {
        .info {
            padding-bottom: 5px;
        }
    }
}

.tableDeposit {
    @media (max-width: $screen-xs-max) {
        border: none;

        .w35 {
            width: 100% !important;
        }

        .fill {
            box-shadow: none;
        }

        td {
            display: block;
            // border-left: 1px solid $g3;
            // border-right: 1px solid $g3;

            &:first-child {
                background-color: $g4;
                border-top: 1px solid $g3;
                border-bottom-color: $red;
            }

            &:not(:first-child) {
                text-align: left;

                &:before {
                    width: 45%; // old browser
                    @include calc(width, '45% - #{$gutter}');
                    padding-left: $gutter;
                    display: inline-block;
                    font-weight: 600;
                    
                    content: attr(data-month) ' months';

                    html[lang="id"] & {
                        content: attr(data-month) ' bulan';
                    }
                }

                &:nth-child(2):before {
                    content: attr(data-month) ' month'
                }

                &:last-child {
                    margin-bottom: $gutter*2;
                }
            }
        }
    }
}

.bbColorRed {
    border-bottom-color: $red;
}

.info {
    display: block;
    @include font-size($size-sm);
    font-weight: 400;
    color: $b5;

    &.infoTotal {
        margin-bottom: $gutter;
    }
}


// terms , tnc

.greyWrap {
    background: $g4;
    padding: 20px;

    li > *:last-child {
        margin-bottom: 0;
    }
}

ol.greyWrap {
    padding-left: 40px !important;
}

.terms .header h1 {margin-bottom: 5px}
