.btn {
    padding: 8px $gutter;
    background-color: $red;
    color: #fff;
    text-align: center;
    border-radius: $radius;
    font-weight: 500;

    &:hover {
        color:#fff;
        background-color: darken($red, 5);
    }

    &-primary {
        color: #fff;
        border-color: transparent;

        a {
            color: #fff;
        }

        &:hover, &:active, &:active:hover {
            border-color: transparent;
            background-color: darken($red, 5);
        }
    }

    &-default {
        border-color: rgba($b5,.6);
        background-color: #fff;
        color: $b2;

        &:hover, &:active, &:active:hover {
            // border-color: rgba($b5,.6);
            background-color: $g5;
            color: $b1;
        }
    }
}

.btn-block {
    width: 100%;
    display: block;
}

// download button
.download {
    list-style: none;

    li {
        display: inline-block;
        margin-bottom: 10px;
        @include transition(all .25s);

        &:first-of-type {
            margin-right: 6px;
        }
    }

    &Badge {
        display: block;
        background-color: $b1;
        padding: $gutter - 5px 20px;
        border-radius: 60px;
        box-shadow: 0 3px 0 rgba(#000,.1);

        &:hover {
            background-color: lighten($b1,10);
        }

        span {
            @include spritePng;
            width: 110px;
            height: 30px;
            display: block;
        }

        &.android span {
            background-position: 0 0;
        }

        &.apple span {
            background-position: -110px 0;
        }

        @media (max-width:359px) {
            padding-left: $gutter;
            padding-right: $gutter;

            span {
                @include transform(scale(.9));
            }
        }
    }

    &--footer {
        .downloadBadge {
            background-color: $b2;

            &:hover {
                background-color: lighten($b2,5);
            }
        }
    }

    &.download--header {
        margin-bottom: $gutter*2;
    }
}

// wrapping the download button
// old
.comingSoon {
    display: inline-block;
    position: relative;

    &:after {
        position: absolute;
        content: 'Coming Soon!';
        top: 50%;
        left: 50%;
        @include transform(translate(-50%, -50%));
        font-weight: 500;
        color: #fff;
        opacity: 0;
        @include transition(all .5s);
    }

    &:hover {
        &:after {
            opacity: 1;
        }

        li {
            @include filter(blur(15px));
            pointer-events: none;
        }
    }
}


// new
.coming-soon {
    font-weight: 500;
    margin-bottom: $gutter;
}

.coming-soon--apple, .coming-soon--android {
    width: 54px;
    height: 54px;
    @include spritePng();
    display: inline-block;
    vertical-align: middle;
    background-color: rgba(#de8b15,.5);
    border-radius: 50%;
    margin-left: 5px;

    .coming-soon--footer & {
        background-color: rgba($b2,.5);
    }
}

.coming-soon--apple { background-position: -63px -160px}
.coming-soon--android { background-position: -3px -160px}

// section CTA
.sectionCTA {
    margin-top: $gutter*2;
}

.btn-feature {
    @include font-size($size-md);
    font-weight: 500;
    color: $b2;

    span {
        width: 36px;
        height: 36px;
        background-color: $red;
        display: inline-block;
        position: relative;
        vertical-align: middle;
        border-radius: 50%;
        top: -2px;
        margin-left: 2px;
        @include transition(all .2s ease-in-out);

        &:before {
            @include spriteSvg;
            background-position: -120px 0;
            display: block;
            width: 10px;
            height: 16px;
            position: absolute;
            content:'';
            top: 50%;
            left: 50%;
            @include transform(translate(-50%, -50%));
        }
    }

    &:hover {

        span {
            @include transform(translateX(3px));
        }

    }
}