.sectionNews {

    blockquote {
        padding: $gutter*2 0 $gutter*2 25%;
        position: relative;
        margin-bottom: 0;

        &:not(:last-child) {
            &:after {
                position: absolute;
                right: 0;
                width: 75%;
                height: 1px;
                background-color: $g3;
                content: '';
                top: 100%;
            }
        }

        @media (max-width: $screen-xs-max) {
            padding-left: 0;

            &:first-child {
                padding-top: 0;
            }

            &:not(:last-child) {
                &:after {
                    width: 100%;
                }
            }
        }
    }

    .quote-meta {
        font-weight: 500;
        @include font-size($size-sm);
        text-transform: uppercase;

        > * {
            display: inline-block
        }
    }

    .source {
        &:before {
            content: '. ';

        }
    }
    
    .quoted-from {
        font-weight: 500;

        img {
            max-width: 140px;
        }

        @media (min-width: $screen-sm) {
            position: absolute;
            left: 0;
            top: $gutter*2;
            width: 25%;
            padding-right: $gutter;
            // text-align: right;
        }

        @media (max-width: $screen-xs-max) {
            margin-bottom: $gutter;
        }
    }
}