
//
// Variables
//

$font-base: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
$font: 'AvenirNext', $font-base;

// color

// shades of gray
$b1: #111; 
$b2: #333; //B2
$b3: #4a4a4a; //- #444
$b4: #666;
$b5: #7a7a7a;
$b6: #999;
$b7: #c6c6c6;

$g1: #a3a19d;
$g2: #d4d1cb;
$g3: #e6e2dd;
$g4: #f7f5f2;
$g5: #fdfbf8;

$red: #ed2d23;
$yellow: #ffb121; //#fdb823

$twitter: #1da1f2;
$facebook: #3b5998;
$instagram: #e1306c;
$whatsapp: #43d854;


// grids and media queries
$gutter: 15px;

// from Bootstrap
//** Deprecated `$screen-xs` as of v3.0.1
$screen-xs:                  480px !default;
//** Deprecated `$screen-xs-min` as of v3.2.0
$screen-xs-min:              $screen-xs !default;
//** Deprecated `$screen-phone` as of v3.0.1
$screen-phone:               $screen-xs-min !default;

// Small screen / tablet
//** Deprecated `$screen-sm` as of v3.0.1
$screen-sm:                  768px !default;
$screen-sm-min:              $screen-sm !default;
//** Deprecated `$screen-tablet` as of v3.0.1
$screen-tablet:              $screen-sm-min !default;

// Medium screen / desktop
//** Deprecated `$screen-md` as of v3.0.1
$screen-md:                  992px !default;
$screen-md-min:              $screen-md !default;
//** Deprecated `$screen-desktop` as of v3.0.1
$screen-desktop:             $screen-md-min !default;

// Large screen / wide desktop
//** Deprecated `$screen-lg` as of v3.0.1
$screen-lg:                  1200px !default;
$screen-lg-min:              $screen-lg !default;
//** Deprecated `$screen-lg-desktop` as of v3.0.1
$screen-lg-desktop:          $screen-lg-min !default;

// So media queries don't overlap when required, provide a maximum
$screen-xs-max:              ($screen-sm-min - 1) !default;
$screen-sm-max:              ($screen-md-min - 1) !default;
$screen-md-max:              ($screen-lg-min - 1) !default;

// font size
$size: 1.6;
$size-xsm :1.2;
$size-sm :1.4;
$size-md: 2;
$size-lg: 3;
$size-xlg: 4.5;
$size-xxlg: 5;
$size-xxxlg: 6;

// font-weight
$regular: 400;
$medium: 600;
$demibold: 500;
$bold: 700;


// radius
$radius: 3px;


//
// Mixin
//

@mixin font-size($fontSize: $size){
    font-size: ($fontSize * 10) + px;
    font-size: $fontSize + rem;
}

@mixin spritePng($hidetext: true) {
    background: url('../images/sprite.png') no-repeat;
    background-size: 320px 300px;
    @if $hidetext {
      @include hide-text;  
    }
}

@mixin spriteSvg() {
    background: url(../images/sprite.svg) no-repeat;
    @include hide-text;
}

@mixin box-sizing($bs: border-box) {
    -moz-box-sizing: $bs;
    box-sizing: $bs;
}

@mixin globalNav(){

    @media(min-width:$screen-sm) {
        padding: 11px $gutter 8px;
        border-radius: 20px;
    }
    
    &:hover {
        background-color: $red;
        color: #fff;
    }

    @media(max-width:$screen-sm-max) {
        padding: 8px 10px 6px;
    } 
}

@mixin title($mq: false){
    font-weight: 700;
    border-top: 6px solid $red;
    @include font-size($size-lg);
    padding-top: $gutter;
    margin-bottom: $gutter;
    display: block;

    @if $mq {
        @media (max-width: $screen-xs-max) {
            margin-bottom: 0;
        }
    }
}
