* {
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

body {
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    color: $b3;
    line-height: 1.5;
}


::-moz-selection {
    color: #fff;
    background-color: $yellow;
}

::-webkit-selection {
    color: #fff;
    background-color: $yellow;
}

::selection {
    color: #fff;
    background-color: $yellow;
}

// check if is webview
.webview {
    .header, .globalNav, .footer {
        display: none;
    }

    &.faq {
        .header {
            background: $yellow;
            display: block;
            padding-top: $gutter*3 - 10px;

            @media (max-width: $screen-xs-max) {
                padding-top: 18px;
                padding-bottom: $gutter*2 - 2px;
            }
        }
    }
}