table {
    border-top: 1px solid $g3;
    width: 100%;
    margin-bottom: $gutter;

    td {
        padding: $gutter 0;
        border-bottom: 1px solid $g3;
        // border-right: 1px solid $g3;
        @include transition(all .2s)
    }

    th {
        padding: $gutter 0;
        border-bottom: 1px solid $g3;
        // border-right: 1px solid $g3;
        @include transition(all .2s);
    }

    tr {
        @include transition(all .2s);
    }

    tfoot {
        td {
            border-bottom: none;
        }
    }
}

.highlight {
    td {
        background-color: rgba($yellow,.1);
    }

    &:hover {
        td {
            background-color: rgba($yellow,.2);
        }
    }
}

.fill {
    background-color: $g4;
    padding: $gutter $gutter*2 $gutter $gutter;
    box-shadow: inset -#{$gutter} 0 0 #fff;
}

.w35 {
    @media (min-width: $screen-sm) {
        width: 35%;
    }
}
