// animations
.animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

.animated.infinite {
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}

.fadeIn {
    @include animation-name(fadeIn);
}

.fadeOut {
    @include animation-name(fadeOut);
}

.fadeInLeft {
    @include animation-name(fadeInLeft);
}

.fadeInRight {
    @include animation-name(fadeInRight);
}

.fadeInUp {
    @include animation-name(fadeInUp);
}

.rotate {
    @include animation-name(rotate);
}

@include keyframes(fadeIn) {
    from {
        opacity: 0;
    } to {
        opacity: 1;
    }
}
@include keyframes(fadeOut) {
    from {
        opacity: 1;
    } to {
        opacity: 0;
    }
}
@include keyframes(fadeInUp) {
    from {
        @include transform(translateY(50px));
        opacity: 0;
    } to {
        @include transform(translateY(0));
        opacity: 1;
    }
}

@include keyframes(fadeInLeft) {
    from {
        @include transform(translateX(-50px));
        opacity: 0;
    } to {
        @include transform(translateY(0));
        opacity: 1;
    }
}

@include keyframes(fadeInRight) {
    from {
        @include transform(translateX(50px));
        opacity: 0;
    } to {
        @include transform(translateY(0));
        opacity: 1;
    }
}

@include keyframes(rotate) {
    from {
        @include transform(rotate(0deg));
    } to {
        @include transform(rotate(360deg));
    }
}

@include keyframes(stRegistering) {
    40% {opacity: 1; @include transform(scale(1))}
    45% {opacity: 0; @include transform(scale(.98))}
    95% {opacity: 0; @include transform(scale(1))}
    100% {opacity: 1; @include transform(scale(1))}
}

@include keyframes(stDone) {
    0% { opacity: 0;}
    40% { opacity: 0;}
    45% { opacity: 1;}
    95% { opacity: 1;}
    100% { opacity: 0;}
}




