// resetting the margins

h1, h2, h3, h4, h5, h6, ol, p, ul, blockquote {
    margin: 0 0 $gutter;
    padding: 0;
}


// headings
h1, h2, h3, h4, h5, h6 {
    font-weight: 500;
    color: $b2;
}

h1 {
    line-height: 1.1;
    @include font-size($size-xlg);

    @media (max-width: $screen-xs-max) {
        @include font-size(2.8);
        line-height: 1.3;
    }
}

h2 {
    @include font-size($size-lg);
    line-height: 1.2;

    @media (max-width: $screen-xs-max) {
        @include font-size($size-md);
        line-height: 1.3;
    }
}

h3, h4 {
    @include font-size($size-md);
    line-height: 1.3;
}

h5 {
    @include font-size();
}


// text styles
.sectionTitle {
    font-weight: 700
}

.lead {
    @media (min-width: $screen-sm) {
        @include font-size($size-md);
    }
}


// hyperlinks
a {
    text-decoration: none;
    @include transition(all .2s);
    color: $red;

    &:hover {
        color: darken($red, 10);
    }
}


// list
ul.list, ol.list {
    padding-left: 20px;

    ul, ol {
        padding-left: 20px;
    }
}

.list--alphabet {list-style-type: lower-alpha}
.list--roman {list-style-type: lower-roman}