.footer {
    background-color: $b1;
    color: $g1;
    @include font-size($size-sm);

    h5 {
        @include font-size($size-xsm);
        text-transform: uppercase;
        color: $yellow;
    }

    a {
        color: #fff;

        &:hover {
            color: $red;
        }
    }

    ul {
        list-style: none;
    }
}

.footerContent {
    padding: $gutter*4 0 $gutter;

    @media (max-width: $screen-sm-max) {
        [class*="col"]:not(:last-child) {
            margin-bottom: $gutter*2;
        }
    }
}

.footerHeader {
    margin-bottom: 20px;
}


.footerLink {
    overflow: hidden;

    ul {
        float: left;
        width: 50%;

        &:first-of-type {
            padding-right: $gutter
        }
    }

    li:not(:last-child) {
        margin-bottom: $gutter;
    }
}

.footerContact {
    li {
        padding-left: 40px;
        position: relative;

        &:first-child {
            margin-bottom: $gutter;
        }

        &:before {
            width: 30px;
            height: 30px;
            @include spriteSvg();
            position: absolute;
            left: 0;
            content: '';
        }

        &.contactPhone {
            &:before {
                top: 2px;
                background-position: 0 -299px;
            }
        }
        &.contactForm {
            &:before {
                top: -5px;
                background-position: -30px -299px;
            }
        }
    }
}

.footerSocial {

    ul {
       overflow: hidden;
       display: inline-block; 
    }
    
    li {
        float: left;

        &:not(:last-child) {
            margin-right: $gutter;
        }
    }

    a {
        display: block;
        // padding: $gutter - 5px 0;
        border-radius: 50%;
        opacity: .5;
        
        span {
            @include spriteSvg;
            width: 20px;
            height: 20px;
            display: block;
        } 

        &:hover {
            opacity: 1;
        }

        &.social__ {
            &tw { 
                // background-color: $twitter;
                // &:hover {
                //     // background-color: lighten($twitter,5)
                // }
            }

            &fb { 
                // background-color: $facebook;
                // &:hover {
                //     background-color: lighten($facebook,5)
                // }
                span {background-position: -20px 0}
            }

            &ig { 
                // background-color: $instagram;
                // &:hover {
                //     background-color: lighten($instagram,5)
                // }
                span {background-position: -40px 0}
            }
        }   
    }

}

.footerBottom {
    color: $b4;
}

.footerCopyright {
    @include font-size($size-sm);
    padding-bottom: $gutter*2;
    // text-align: right;
    line-height: 40px;
}