.section--red {
    background-color: $red;
    color: #fff;

    h2, h3, h4, h5, h6 {
        color: inherit;
    }

    .btn-feature {
        color: inherit;

        span {
            background-color: #fff;
            
            &:before {
                background-position: -70px 0;
            }
        }
    }
}

.section--grey {
    background-color: $b1;
    color: #fff;

    h2, h3, h4, h5, h6 {
        color: inherit;
    }
}

.section--yellow {
    background-color: $yellow;
    color: $b1;

    h2, h3, h4, h5, h6 {
        color: inherit;
    }
}

.section--white {
    background-color: #fff;
}

.section--common {
    padding: $gutter*4 0;

    @media (max-width: $screen-xs-max) {
        padding-top: $gutter*2;
        padding-bottom: $gutter*2;
    }
}