section[class*="about"] {
    padding: $gutter*4 0;

    @media(max-width: $screen-xs-max) {
        padding-top: $gutter*3;
        padding-bottom: $gutter*3;
    }
}

.aboutBackground {
    height: 100px;
    background: $yellow;
    box-shadow: inset 0 -20px 0 lighten(#f7a521,2), inset 0 -22px 0 $yellow, inset 0 -42px 0 lighten(#f19e05,1);
    position: relative;

    &:after {
        position: absolute;
        width: 99%;
        height: 188px;
        bottom: -41px;
        left: 0;
        background: url(../images/aboutBG.png) no-repeat 73% bottom;
        background-size: 488px 188px;
        content: '';
    }

    @media (max-width: $screen-xs-max) {
        margin-top: -$gutter*2;
        height: 110px;
        box-shadow: inset 0 -14px 0 lighten(#f7a521,2), inset 0 -16px 0 $yellow, inset 0 -30px 0 lighten(#f19e05,1);

        &:after {
            bottom: -30px;
            background-size: 360px 139px;
            background-position: 90% bottom;
        }
    }
}


//section
.aboutVideo {
    .container {
        position: relative;
    }
}

.about-video, .background-video, .about-iframevid {
    @media (min-width: $screen-sm) {
        height: 422px;
    }
    
    @media (min-width: $screen-md) {
        height: 546px;
    }

    @media (min-width: $screen-lg) {
        height: 659px;
    }
}

.background-video {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
}

.btn-play {
    position: absolute;
    top: 50%;
    left: 50%;
    @include transform(translate(-50%, -50%));
    width: 90px;
    height: 90px;
    border: 3px solid #fff;
    border-radius: 50%;
    // @include linear-gradient(135deg, $red 49.99%, darken($red, 10) 50%);
    opacity: .9;

    &:before {
        width: 22px;
        height: 30px;
        @include spriteSvg();
        background-position: -92px -300px;
        content: '';
        position: absolute;
        top: 50%;
        left: 52%;
        @include transform(translate(-50%, -50%));
    }

    &:hover {
        opacity: 1;
        background-color: $red;
        border-color: $red;
        box-shadow: 0 1px 10px rgba(#000,.2);
        @include transform(translate(-50%, -50%) scale(1.1));
    }
}

.about-iframevid {
    width: 100%;
    min-height: 300px;
}



// awards
.awards {
    list-style: none;
    text-align: center;
    margin-top: $gutter*2;

    li {
        display: inline-block;
        margin: 0 10px $gutter*2;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.awardTitle {
    width: 141px;
    height: 91px;
    padding: 0 24px;
    @include spritePng(false);
    background-position: 0 -66px;
    @include display(flex);
    @include align-items(center);
    @include justify-content(center);
    @include font-size(1.3);
}

.awardName {
    @include font-size($size-sm);
    font-weight: 500;
    margin-top: 10px;
}

.awardYear {
    @include font-size(1.1);
    font-weight: 400;
}
