// Download
.registerDownload {
    padding-bottom: $gutter*2;

    @media (max-width: $screen-xs-max) {
        padding-top: 5px;

        .sectionTitle {
            margin-bottom: 3px;
            font-weight: 500;
            @include font-size(1.6);

            .red { color: $b2;}
        }
    }
}


// Steps
.registerSteps {
    
    @media (min-width: $screen-sm) {
        padding-bottom: $gutter*4;
    }

    .section--white {
        border-radius: $radius;
        position: relative;
        box-shadow: 0 1px 5px rgba(#000,.1);
        
        // @media (min-width: $screen-md) {
        //     background-image: url(../images/hand-holding-phone.png);
        //     background-repeat: no-repeat;
        //     background-position: -40px bottom;
        //     background-size: 480px auto;
        // }

        // @media (min-width: $screen-lg) {
        //     background-position: 0 bottom;
        // }

        @media (max-width: $screen-sm-max) {
            margin-left: -$gutter;
            margin-right: -$gutter;
            padding: 0 $gutter;
        }
    }

}

.steps {
    list-style: none;
    margin-bottom: 0;

    li {
        padding: 10px 0 0 23px;
        position: relative;

        &:before {
            content: attr(data-step);
            padding-right: 10px;
            @include font-size($size-md);
            vertical-align: middle;
            font-weight: 500;
            color: $red;
            margin-left: -23px;
        }

        a {
            color: $b2;
        }

        &.active {
            a {
                color: $red;
                font-weight: 500;
            }
        }

        @media (min-width: $screen-lg) {
            width: 80%;
        }

        @media (max-width: $screen-sm-max) {
            padding: 0;
            display: inline-block;
            margin: 0 2px $gutter;

            &:before {
                position: absolute;
                left: 52%;
                top: 53%;
                @include transform(translate(-50%, -50%));
                padding-right: 0;
                @include font-size($size-xsm);
                cursor: pointer;
                margin-left: 0;
            }

            a {
                width: 24px;
                height: 24px;
                border: 1px solid $red;
                display: block;
                @include hide-text;
                border-radius: 50%;
            }

            &.active {
                a {
                    background-color: $red;
                }

                &:before {
                    color: #fff;
                }
            }
        }
    }
}

.stepsPhone {
    background: url(../images/iphone-white.png) no-repeat;
    width: 246px;
    height: 500px;
    background-size: 100% auto;
    @include transform(translate(76px, 55px));

    @media (min-width: $screen-lg) {
        @include transform(translate(116px, 55px));
    }

    @media (max-width: $screen-sm-max) {
        @include transform(translate(0));
        margin: $gutter*2 auto;
    }
}

.stepsContent {
    padding-bottom: 30px;

    @media (min-width: $screen-sm) {
        padding: 0 0 30px;
    }

    @media (min-width: $screen-md) {
        padding: $gutter*9 0 $gutter*12;
    }

    @media (max-width: $screen-sm-max) {
        text-align: center;

        h2 {
            display: none;
        }
    }
}


