// new Customer
.newCustomer {
    padding: 20px 0 15px 42px;
    @include font-size($size-sm);
    position: relative;

    h5 {
        margin-bottom: 3px;
        @include font-size($size-sm);
    }

    &:before {
        position: absolute;
        content: '';
        left: 0;
        top: 22px;
        width: 30px;
        height: 30px;
        @include spriteSvg();
        background-position: -60px -299px;
    } 

    @media(min-width: $screen-md) {
        border-top: 1px solid rgba($g3,.7);
        margin: 0 0 $gutter $gutter;
        position: absolute;
        right: 15px;
        bottom: 0;
        left: 0;
        border-radius: $radius $radius 0 0;
    }

    @media(max-width:$screen-sm-max) {
        background-color: rgba($yellow,.15);
        text-align: left;
        margin: $gutter*2 -#{$gutter} -#{$gutter*2};
        padding: 20px 26% 15px;

        &:before {
            left: 19.5%;
        }
    }

    @media(max-width:$screen-xs-max) {
        padding-left: 60px;
        padding-right: 20px;

        &:before {
            left: 20px;
        }
    }
}


// Success
.registerNewCustomer {
    padding: $gutter*4 0;
}

.registerYoutube {
    @include font-size($size-sm);
    margin-top: $gutter*2;

    span {
        display: inline-block;
        width: 78px;
        height: 34px;
        @include spritePng();
        background-position: -141px -66px;
        float: left;
        margin: 3px $gutter 0 0;
    }

    p {
        float: left;
        @include calc(width, '100% - 93px');
    }
}