.screen {
    img {
        display: block;
    }
}

.content {
    @media(max-width: $screen-sm-max) {
        margin: $gutter*3 0;
    }
}



// Debit Card
.featureDebitCard {
    .content {
        @media(min-width: $screen-md) {
            margin-top: 20px;
        }
    }
}

.screenDebitCard {
    
    position: relative;
    text-align: center;

    @media (min-width: $screen-md) {
        margin-top: -$gutter*6;
        
    }

    @media (max-width: $screen-sm-max) {
        margin: $gutter 0 $gutter*2;

        // &:before {
        //     content: '';
        //     position: absolute;
        //     width: 320px;
        //     height: 100%;
        //     @include linear-gradient(135deg, rgba(#fff,.5) 0%, rgba(#fff,.1) 49.99%, rgba(#fff,0) 50%);
        //     background-color: transparent;
        //     border-radius: 5px;
        //     top: 0;
        //     left: 0;
        //     z-index: 1;
        // }
    }

    img {
        &:first-child {
            width: 290px;
            margin: 0 auto;
            @include animation(fadeInUp 1s ease-in-out both);

            @media (min-width: $screen-md) {
                margin-bottom: -62px;
            }

            // @media (max-width: $screen-sm-max) {
            //     display: none;
            // }
        }
    }

    .debitCard { 
        width: 320px;
        border-radius: 12px;
        box-shadow: 0 1px 5px rgba(#000, .3);
        @include animation(fadeInUp 1s ease-in-out both);

        @media (min-width: $screen-md) {
            @include animation(fadeInRight 1s ease-in-out .5s both);
            position: absolute;
            top: 35%;
            left: 10%;
        }
        

        // @media (max-width: $screen-sm-max) {
        //     // margin: 0 auto;
        // }
    }
}


// Soft Token
.featureSoftToken {
    padding-bottom: $gutter*4;

    .content {
        @media (min-width: $screen-md) {
            margin-top: $gutter*13;
        }
    }
}

.screenSoftToken {
    width: 290px;
    height: 500px;
    background: url(../images/screen-softtoken.png) no-repeat top;
    background-size: 290px 500px;

    > * {
        top: -22px;
        left: 15px;
        width: 262px;
    }

    .spinner {
        bottom: 102px;
        width: 44px;
        height: 44px;
        margin-left: -22px;
    }

    @media(max-width: $screen-sm-max) {
        margin: 0 auto;
        width: 260px;
        height: 448px;
        background-size: 260px 448px;

        > * {
            top: -20px;
            left: 13px;
            width: 235px;
        }

        .spinner {
            bottom: 92px;
            width: 39px;
            height: 39px;
            margin-left: -20px;
        }
    }
}


// Virtual Assistant
.featureVA {
    .content {
        @media (min-width: $screen-md) {
            margin-top: $gutter*10;
        }
    }
}

.screenVA {

    @media (min-width: $screen-md) {
        margin-bottom: -$gutter*7;
    }

    img {
        position: relative;
        top: -45px;
        width: 290px;

        @media (max-width: $screen-sm-max) {
            margin: 0 auto -45px;
            width: 260px;
        }
    }   
}


// PFM
.featurePFM {
    padding-top: $gutter*4;

    .content {
        @media (min-width: $screen-md) {
            margin-top: $gutter*8;
        }
    }

    @media (max-width: $screen-sm-max) {
        padding-top: $gutter*3;
    }
}

.screenPFM {
    
    @media (min-width: $screen-md) {
        height: 480px;
        overflow: hidden;
    }

    img {
        width: 290px;

        @media (max-width: $screen-sm-max) {
            margin: 0 auto;
            width: 260px;
        }
    }
}