.faq, .faq-results {
    .header {
        padding-bottom: $gutter*3;
    }
}

.sectionFaq {
    @include linear-gradient(to bottom, $g4 0, #fff 100%);
    background-size: 100% 100px;
    background-repeat: no-repeat;
    background-color: #fff;
    padding-bottom: $gutter*2;

    .lbl-content-box {
        margin: 20px 0;


        h4 {
            @include title();

            @media(max-width: $screen-xs-max) {
                @include font-size(2.4);
                line-height: 1.4;
            }
        }

        @media (max-width:$screen-xs-max) {
            .row {
                margin-left: 0;
                margin-right: 0;
            }

            .faq-full-content.hide {
                display: block !important;
            }

            .inside-content-box .mob-content-box {
                display: none;
            }
        }
    }

    .inside-content-box {
        @include clearfix;

        > label {
            display: block;
            position: relative;
        }

        .title {
            display: block;
            font-weight: 500;
            margin-bottom: 10px;
            border-top: none;
            padding: 0;
            @include font-size(1.6);
            line-height: 1.5;
        }

        @media (min-width: $screen-sm) {
            margin-bottom: $gutter*2;
        }

        @media (max-width: $screen-xs-max) {

            padding-left: 0;
            padding-right: 0;

            &:not(:last-child) {
                border-bottom: 1px solid $g3;
            }

            label {
                cursor: pointer;
            }

            .title {
                padding: 15px 30px 12px 0;
                margin-bottom: 0;
            }
        }
    }
}

.search-box {
    position: relative;
    @include transform(translateY(-50%));

    // @media (min-width: $screen-md) {
    //     width: 66.66%;
    // }

    input {
        border: none;
        width: 100%;
        display: block;
        padding: 22px $gutter*4 20px 15px;
        border-radius: $radius;
        background: #fff;
        box-shadow: 0 1px 4px rgba(#000,.2);
        @include transition(all .1s);
        border-bottom: 2px solid transparent;
        -webkit-appearance: none;

        &:focus, &:active {
            outline: 0;
            border-bottom-color: $yellow;
        }

        @media (max-width: $screen-xs-max) {
            padding-top: 18px;
            padding-bottom: 16px;
        }
    }

    .btn-search {
        position: absolute;
        @include spriteSvg();
        background-position: 0 -196px;
        width: 26px;
        height: 26px;
        right: 20px;
        top: 50%;
        @include transform(translateY(-50%));

        @media (max-width:$screen-xs-max) {
            @include transform(translateY(-50%) scale(.9));
        }
    }

    @media (max-width: $screen-xs-max) {
        margin-bottom: -$gutter;
    }
}

.see-more {
    float: right;
    margin-top: 10px;

    span {
        width: 10px;
        height: 6px;
        display: inline-block;
        @include spriteSvg();
        background-position: -90px 0;
        margin-left: 3px;
        position: relative;
        top: -1px;
    }
}

.faq-results {
    .inside-content-box {
        @media (min-width: $screen-sm) {
            min-height: 250px;
        }
    }
}