.sectionTnc {
    padding: $gutter*2 0;

    @media(min-width: $screen-sm) {
        padding-top: $gutter*4;
        padding-bottom: $gutter*4;
    }

    // Quick links 
    @at-root .tnc-quicklinks {
        // padding-left: $gutter*2;
        // background-color: $g5;
        margin-top: -$gutter*2;
        margin-bottom: $gutter*3;
        // box-shadow: 0 1px 5px rgba(#000,.1);
        opacity: 0;
        @include animation(tnc .5s ease-out forwards);
        @include font-size($size-sm);
        // border-left: 3px solid $red;

        h5 {
            margin-bottom: 0;
            @include font-size($size-sm);
        }


        ul {
            list-style: none;
            margin-bottom: 0;
        }

        li {
            font-weight: 500;
            padding: 8px 0;
            
            &:last-child{
                padding-bottom: 0;
            }
        }

        a {
            font-weight: 400;
        }
    }

    // @at-root .tnc-quicklinks, .tnc-content {
    //     @media(min-width: $screen-lg) {
    //         padding-left: 10%;
    //         padding-right: 10%;
    //     }
    // }

    @include keyframes(tnc){
        from {
            opacity:0;
            @include transform(translateY(20px))
        } to {
            opacity:1;
            @include transform(translateY(0))
        }
    }

    // TnC
    .title {

        @media(max-width: $screen-xs-max) {
            cursor: pointer;
            border-top: 1px solid $g3;
        }
    }

    .inside-content-box {

        label {
            display: block;
            position: relative;
        }

        @media (min-width: $screen-sm) {
            padding: $gutter*2 0;

            &:first-child {
                padding-top: 0;
            }
        }

        // @media (min-width: $screen-md) {
        //     padding-left: 10%;
        //     padding-right: 10%;
        // }
    }

    .mob-content-box {
        ol {
            padding-left: 20px;
        }
        li {
            padding-left: 5px;

            &:not(:last-child) {
                margin-bottom: $gutter;
            }
        }

        ul, ol {
            ul, ol {
                margin-top: $gutter;
            }
        }

        @media(max-width: $screen-xs-max) {
            display: none;
            padding-bottom: $gutter;

            > *:last-child {
                margin-bottom: 0;
            }
        }
    }

}
